		<nav class="flex h-full w-full items-center">
			<div class="md:hidden">
				<Hamburger bind:open={sidebar}/>
			</div>
			<div class="text-3xl font-semibold mx-3 flyvec"><a href="https://arxiv.org/abs/2101.06887" target="_blank">FlyVec</a></div>
		</nav>
	
		<Menu/>
	</header>
